@use "../foundation/variables" as *;

// 
.l-header {
  background-color: $green;
  box-shadow: 0 2px 4px 1px rgba(0, 0, 0, 0.2);
  filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.5));

  &-logo {
    width: 100%;
    background-color: $light-green;
    &__inner {
      display: grid;
      grid-template-columns: 180px auto 1fr;
      align-items: center;
      gap: 0.5rem;
      max-width: calc(1280px);
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      height: 40px;
      padding: 0.5rem 1.5rem;
    }
    &--right {
      text-align: right;
      font-size: 10px;
      display: grid;
      grid-template-columns: auto auto;
      justify-content: right;
    }
  }
}

// 
.l-header__wrapper {
  width: calc(1280px);
  color: white;
  display: grid;
  grid-template-columns: 400px 253px 253px 1fr;
  padding: 1.5rem;
  margin-left: auto;
  margin-right: auto;
}
.l-header__right,
.l-header__center {
  border-left: 1px solid white;
}

.l-header__center {
  margin-left: 1.5rem;
  padding-left: 1.5rem;
}
.l-header__center-right {
  padding-right: 1.5rem;
}
.l-header__right {
  padding-left: 1.5rem;
}

// 
.l-header__left {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: repeat(2, auto);
}

.l-header__left-item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid white;
  padding-bottom: 0.25rem;
}

.l-header-admin-info {
  //
  &__date {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  // 
  &__dayOfWeek {
    font-size: 12.7px;
    line-height: 20px;
    border-radius: 50%;
    background: white;
    color: $green;
    width: 20px;
    height: 20px;
    display: inline-block;
    text-align: center;
    font-weight: 700;
    // 
    margin-top: 0.25rem;
  }
  // 
  &__open-close {
    font-size: 0.875rem;
    gap: 1rem;
  }
}

.l-header-admin-info__open-close,
.l-header-admin-info__loginUserName {
  height: 2.25rem;
  line-height: 2.25rem;
  padding-top: 0.25rem;
}
// 

// 
.l-header__center {
  font-size: 0.75rem;
  &--info {
    display: grid;
    grid-template-columns: auto 1fr;
  }
}

// 
.l-header__right {
  &-item {
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 1rem;
    font-size: 0.875rem;
  }
}

// 
.l-header-user {
  background: $light-green;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  justify-content: center;
  height: 75px;
  align-items: center;
  padding: 0 1.5rem;
  margin-bottom: 2rem;
  img {
    width: 187px;
  }

  @media screen and (min-width: 850px) {
    gap: 1rem;
    flex-direction: row;
    justify-content: flex-start;
    margin-bottom: 2.5rem;
  }
}
